import { forwardRef, useReducer } from "react"
import { IconButton, InputAdornment, OutlinedInput } from "@rp/react/web/mui/core"
import { VisibilityOffOutlined, VisibilityOutlined } from "@rp/react/web/mui/icons"

type PasswordInputProps = React.ComponentProps<typeof OutlinedInput>

const PasswordInput = forwardRef(
  (props: Omit<PasswordInputProps, "type" | "endAdornment">, ref) => {
    const [showPassword, toggleShowPassword] = useReducer(show => !show, false)
    const Icon = showPassword ? VisibilityOffOutlined : VisibilityOutlined
    const inputType = showPassword ? "text" : "password"

    return (
      <OutlinedInput
        ref={ref}
        type={inputType}
        endAdornment={
          <InputAdornment position="end">
            <IconButton
              aria-label="toggle visibility"
              onClick={toggleShowPassword}
              onMouseDown={evt => evt.preventDefault()}
              edge="end"
            >
              <Icon sx={theme => ({ color: theme.palette.grey[300] })} />
            </IconButton>
          </InputAdornment>
        }
        {...props}
      />
    )
  },
)

export { PasswordInput }
